.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 100;
    visibility: hidden;
  }
  
.active{
  visibility: visible;
}

  .modal-content {
    background-color: #fff;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    max-width: 1000px;
    max-height: 80%;
    overflow: auto;
  }

.title{
  justify-content: center;
}

.paragrapgh{
  line-height: 1.4;
}

.info-picture{
  max-width: 800px;
}